import React, { useState, useEffect } from "react"
import styled from "styled-components"

import DoubleLeft from "../assets/double-left.svg"
import DoubleRight from "../assets/double-right.svg"
import Left from "../assets/left.svg"
import Right from "../assets/right.svg"

const CONTAINER = styled.div`
    display: flex;
    width: 300px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`

const HEADER = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 10px;
`

const IMG = styled.img`
    height: 30px;
    cursor: pointer;
`

const YEAR = styled.div`
    display: flex;
    justify-content: center;
`

const MONTH = styled.div`
    display: flex;
    justify-content: center;
`

const NAMEDAYS = styled.div`
    display: flex;
    width: 100%;
    margin-bottom: 5px;
`

const NAMEDAY = styled.div`
    flex: 1 1 14%;
    display: flex;
    justify-content: center;
`

const DAYS = styled.div`
    display: flex;
    width: 100%;
    flex-wrap: wrap;
`

const DAY = styled.div`
    flex: 1 1 14%;
    display: flex;
    justify-content: center;
`

function Datepicker () {
    const [date, setDate] = useState(new Date())
    const [year, setYear] = useState(date.getFullYear())
    const [month, setMonth] = useState(date.getMonth())
    const [nbDays, setnbDays] = useState(40 - new Date(year, month, 40).getDate())
    const [firstDay, setfirstDay] = useState(new Date(year, month, 1).toDateString().substring(0,3))
    const [monthDays, setMonthDays] = useState([])

    const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"]
    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]

    const nextMonth = () => {
        setMonth(month + 1)
        monthDetails()
    }

    const monthDetails = () => {
        const test = []
        const indexBegin = days.indexOf(firstDay)
        const endPreviousMonth = 40 - new Date(year, month - 1, 40).getDate()
        console.log(indexBegin)
        console.log(endPreviousMonth)
        for (let i = indexBegin-1; i >= 0; i--) {
            test.push(endPreviousMonth - i)
        }
        for (let i = 1; i <= nbDays; i++) {
            test.push(i)
        }
        const endArray = 42 - (test.length)
        for (let i = 1; i <= endArray; i++) {
            test.push(i)
        }
        setMonthDays(test)
    }

    useEffect(() => {
        monthDetails()
    },[])

    return (
        <CONTAINER>
            <HEADER>
                <IMG src = {DoubleLeft}/>
                <IMG src = {Left}/>
                <div>
                    <YEAR>{year}</YEAR>
                    <MONTH>{months[month]}</MONTH>
                </div>
                <IMG onClick = {() => nextMonth()} src = {Right}/>
                <IMG src = {DoubleRight}/>
            </HEADER>
            <NAMEDAYS>
                {days.map((day, index) => (
                    <NAMEDAY key = {index}>
                        {day}
                    </NAMEDAY>
                ))}
            </NAMEDAYS>
            <DAYS>
                {monthDays.map((monthDay, index) => (
                    <DAY key = {index}>
                        {monthDay}
                    </DAY>
                ))}
            </DAYS>
        </CONTAINER>
    )
}

export default Datepicker